import { h } from 'preact';
import SelectSearch from 'react-select-search';

const Select = ({ options,placeholder,isSearch,value,handleOptionChange,className,handleSearchOptions, }) => {
	return (
		<div>
			<SelectSearch options={options} getOptions={handleSearchOptions} search={isSearch} value={value} name="fund" placeholder={placeholder} onChange={handleOptionChange} className={className} />
		</div>
	);
};

export default Select;