//Calculations
const calculateIrate = ({
  duration,
  interestRate,
})=>{
 return duration === "yearly"
 ? interestRate
 : duration === "monthly"
 ? interestRate / 12
 : duration === "halfyearly"
 ? interestRate / 2
 : interestRate / 4;
};

const calculatePeriod = ({
  duration,
  investmentPeriod
})=>{
  return duration === "yearly"
  ? investmentPeriod
  : duration === "monthly"
  ? investmentPeriod * 12
  : duration === "halfyearly"
  ? investmentPeriod * 2
  : investmentPeriod * 4;

};

export const calculatedTotalInvestment = ({
  investmentAmount,
  ...restState
})=>{
 const irate = calculateIrate(restState);
 const period = calculatePeriod(restState);
 return parseInt(
  investmentAmount * Math.pow(1 + irate / 100, period)
 )
};

export const calculateInterestRate = ({
  citizen
})=>{
  return citizen === "senior" ? 8.6 : 8
};

export const calculatedMaturityAmount = ({
  investmentAmount,
  ...restState
})=>{
  const irate = calculateIrate(restState);
  const period = calculatePeriod(restState);
  return parseInt(investmentAmount * Math.pow(1 + irate / 100, period)) - investmentAmount
};

export const calculateDisplayInterest = ({
  investmentAmount,
  ...restState
})=>{
  const irate = calculateIrate(restState);
  const period = calculatePeriod(restState);
  return parseInt(
    investmentAmount * Math.pow(1 + irate / 100, period) -
      investmentAmount
  )
};

export const calculateDisplayMaturityAmt = ({
  investmentAmount,
  ...restState
})=>{
  const irate = calculateIrate(restState);
  const period = calculatePeriod(restState);
  return parseInt(
    investmentAmount * Math.pow(1 + irate / 100, period)
  )
};
      
export const calculateMaturityData = ({
  interestRate,
  duration,
  investmentPeriod,
  investmentAmount
}) => {
  let result = [{ x: 0, y: 0 }];
  let i = 1;
  const irate =
  duration === "yearly"
    ? interestRate
    : duration === "monthly"
    ? interestRate / 12
    : duration === "halfyearly"
    ? interestRate / 2
    : interestRate / 4;

  for (i = 1; i <= investmentPeriod; i++) {
    const period =
    duration === "yearly"
      ? i
      : duration === "monthly"
      ? i * 12
      : duration === "halfyearly"
      ? i * 2
      : i * 4;
    var y = (
      parseInt(investmentAmount * Math.pow((1 + (irate / 100)), period))
    );
    result.push({ x: i, y: y });
  }
  return result;
};